  grid-column-start: 2;
  grid-row-start: 2;
}

.promo__link {
  width: 343px;
  padding: 29px;
  text-align: center;
  color: #ffffff;
  border: 4px solid #ffffff;
  text-transform: uppercase;
}
.promo img {
  height: 100%;
}

